@charset "UTF-8";

body {
  margin: 0;
  padding: 0;
}

.box {
  height: 65px;
  display: flex;
  justify-content: space-between;
  font-family: sans-serif;
  /* тут можно вставить и другие шрифты google font в помощь */
  min-width: 1200px;
  margin-top: 5px;
}

.left-bg {
  display: flex;
  background-image: linear-gradient(to right, transparent, rgba(247, 203, 27, 0.9) 50%);
  width: 45%;
}

.right-bg {
  display: flex;
  background-image: linear-gradient(to left, transparent, rgba(242, 59, 4, 0.9) 50%);
  width: 45%;
}

.left {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  align-self: center;
  font-size: 45px;
  background-color: black;
  color: #fff;
  padding: 0 10px;
  margin-left: 20px;
  margin-right: 20px;
}

.left::before {
  content: '';
  position: absolute;
  top: 0;
  left: -20px;
  border-top: 65px solid black;
  border-left: 20px solid transparent;
}

.left::after {
  content: '';
  position: absolute;
  top: 0;
  right: -20px;
  border-bottom: 65px solid black;
  border-right: 20px solid transparent;
}

.left_left {
  height: 80px;
  margin-right: 23px;
}

.left_left::before {
  border-top: 80px solid black;
  left: -24px;
  border-left: 24px solid transparent;
}

.left_left::after {
  border-bottom: 80px solid black;
  right: -24px;
  border-right: 24px solid transparent;
}

.right {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  align-self: center;
  font-size: 45px;
  background-color: black;
  color: #fff;
  padding: 0 10px;
  margin-left: 20px;
  margin-right: 20px;
}

.right::before {
  content: '';
  position: absolute;
  top: 0;
  left: -20px;
  border-bottom: 65px solid black;
  border-left: 20px solid transparent;
}

.right::after {
  content: '';
  position: absolute;
  top: 0;
  right: -20px;
  border-top: 65px solid black;
  border-right: 20px solid transparent;
}

.right-right {
  height: 80px;
  margin-left: 23px;
}

.right-right::before {
  left: -24px;
  border-left: 24px solid transparent;
  border-bottom: 80px solid black;
}

.right-right::after {
  right: -24px;
  border-right: 24px solid transparent;
  border-top: 80px solid black;
}

.player {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  padding: 0 30px;
}

.player__name {
  display: block;
  font-weight: 600;
  text-transform: uppercase;
}

.player__deck {
  display: block;
}

.player__name_left {
  text-align: right;
}

.player__deck_left {
  text-align: right;
}

.timer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 10%;
  position: relative;
  align-self: center;
  font-size: 45px;
  background-color: black;
  color: #fff;
  padding: 0 10px;
  margin-left: 20px;
  margin-right: 20px;
}

.timer::before {
  content: '';
  position: absolute;
  left: -20px;
  background-color: rgba(247, 203, 27, 0.9);
  border-top: 65px solid black;
  border-left: 20px solid transparent;
}

.timer::after {
  content: '';
  position: absolute;
  background-color: rgba(242, 59, 4, 0.9);
  right: -20px;
  border-top: 65px solid black;
  border-right: 20px solid transparent;
}